.container {
    position: relative;
    width: 150;
    max-width: 150;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .container img {
    width: 150;
    height: 80;
  }
  
  .container .btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    padding: 8px,8px;
  }

  .center-img{
    display: flex;
    align-items: center;
    justify-content: center;
  }
